import {
  _decorator,
  Component,
  Collider2D,
  Contact2DType,
  IPhysics2DContact,
} from "cc";

import { GameManager } from "./GameManager";

const { ccclass } = _decorator;

@ccclass("TestContactCallBack")
export class TestContactCallBack extends Component {
  private colliding: boolean = false;
  private collisionStayTime: number = 0;
  private collisionStayThreshold: number = 1.0; // Define the duration for collision stay (in seconds)

  start() {
    // Registering callback functions for a single collider
    let collider = this.getComponent(Collider2D);
    if (collider) {
      collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
      collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
    }
  }

  onBeginContact(
    selfCollider: Collider2D,
    otherCollider: Collider2D,
    contact: IPhysics2DContact | null
  ) {
    console.log("onBeginContact");
    this.colliding = true;
    this.collisionStayTime = 0; // Reset the collision stay time

    // Start an update loop to simulate "onCollisionStay" behavior
    this.schedule(this.checkCollisionStay, 0.1); // Adjust the interval as needed
  }

  onEndContact(
    selfCollider: Collider2D,
    otherCollider: Collider2D,
    contact: IPhysics2DContact | null
  ) {
    console.log("onEndContact");
    this.colliding = false;

    // Stop the update loop when collision ends
    this.unschedule(this.checkCollisionStay);
  }

  checkCollisionStay(dt: number) {
    if (this.colliding) {
      // Increment the collision stay time while colliders are in contact
      this.collisionStayTime += dt;

      // Check if collision stay time exceeds the threshold
      if (this.collisionStayTime >= this.collisionStayThreshold) {
        console.log("Collision Stay for a long time!");

        GameManager.getInstance().gameOver();

        // Perform actions or logic when colliders stay in contact for a long time
      }
    }
  }
}
