import {
  _decorator,
  Component,
  Collider2D,
  Contact2DType,
  IPhysics2DContact,
  Vec2,
  Node,
  Vec3,
} from "cc";
import { AudioManager } from "./AudioManager";
import { BallGenerator } from "./ballGenerator";
import { GameManager } from "./GameManager";

const { ccclass, property } = _decorator;

@ccclass("Ball")
export class Ball extends Component {
  private ballNumber: number = 1;
  private mergeable: boolean = true;
  private topDangerZone: Node = null;

  start() {
    let collider = this.getComponent(Collider2D);
    if (collider) {
      collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
    }
    this.ballNumber = +this.node.name;
  }

  onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D) {
    //console.log("Ball collided with: " + otherCollider.node.name);
    if (this.mergeable && otherCollider.node.name === selfCollider.node.name) {
      // this.mergeable = false;
      this.scheduleOnce(() => {
        this.merge(otherCollider.node.getComponent(Ball)); //just
      }, 0.1); // Adjust the delay time as needed
    } else {
      AudioManager.getInstance().playSoundDroppedBall();
    }
  }
  // Function to check if the ball reaches the top danger zone
  checkBallPosition(ballPosition: Vec3): boolean {
    if (!this.topDangerZone) {
      console.warn("Top Danger Zone node not assigned!");
      return false;
    }

    // Get the position of the top danger zone

    // Compare the y positions to check if the ball reaches the top danger zone
    if (ballPosition.y >= this.topDangerZone.position.y) {
      return true; // Ball reached or passed the top danger zone
    }

    return false; // Ball is below the top danger zone
  }

  merge(otherBall: Ball | null) {
    this.checkBallPosition(otherBall.node.position);
    if (otherBall) {
      const currentCount = +this.node.name;
      GameManager.getInstance().updateScore(currentCount);
      BallGenerator.getInstance().blast(this.node.position);
      AudioManager.getInstance().playAnimalSound(+this.node.name);
      BallGenerator.getInstance().spawnBallAtCollision(
        this.node.position,
        this.ballNumber
      );
      //  this.mergeable = true;
      // Destroy the collided prefabs
      this.node.active = false;
      otherBall.node.active = false;
    }
  }
}
