import { _decorator, AudioSource, AudioClip, Component, randomRange } from "cc";
const { ccclass, property } = _decorator;

@ccclass("AudioManager")
export class AudioManager extends Component {
  private static _instance: AudioManager | null = null;

  public static getInstance(): AudioManager {
    return this._instance!;
  }

  @property({ type: AudioSource })
  audioSource: AudioSource | null = null;

  @property({ type: AudioClip })
  backgroundMusic: AudioClip | null = null;
  @property({ type: AudioClip })
  backgroundMusicCritical: AudioClip | null = null;
  @property({ type: AudioClip })
  ballDroppingFx: AudioClip | null = null;
  @property({ type: AudioClip })
  droppedSoundFxs: AudioClip[] = []; //random.range[0,1]| null = null;
  @property({ type: AudioClip })
  animalSoundFxs: AudioClip[] = []; //random.range[0,1]| null = null;

  @property({ type: AudioClip })
  soundBlastEffect: AudioClip | null = null;

  onLoad() {
    AudioManager._instance = this;
    this.playBackgroundMusic();
  }

  playBackgroundMusic() {
    if (this.audioSource && this.backgroundMusic) {
      this.audioSource.clip = this.backgroundMusic;
      this.audioSource.play();
      // this.audioSource.loop = true;
      this.audioSource.volume = 0.5;
    }
  }
  playBackgroundMusicDanger() {
    this.audioSource.stop();
    if (this.audioSource && this.backgroundMusicCritical) {
      this.audioSource.clip = this.backgroundMusicCritical;
      this.audioSource.play();
      // this.audioSource.loop = true;
      this.audioSource.volume = 0.5;
    }
    return;
  }
  playBackgroundMusicCritical() {
    if (this.audioSource && this.backgroundMusicCritical) {
      this.audioSource.clip = this.backgroundMusicCritical;
      this.audioSource.play();
    }
  }
  playBallDropping() {
    if (this.audioSource && this.ballDroppingFx) {
      this.audioSource.clip = this.ballDroppingFx;
      this.audioSource.play();
    }
  }
  playSoundDroppedBall() {
    //fx=this.dropeedSoundFxs[random.range[0,1]]
    if (this.audioSource && this.droppedSoundFxs[randomRange(0, 3)]) {
      this.audioSource.clip = this.droppedSoundFxs[randomRange(0, 3)];
      this.audioSource.play();
    }
  }

  playSoundBlastEffect() {
    if (this.audioSource && this.soundBlastEffect) {
      this.audioSource.clip = this.soundBlastEffect;
      this.audioSource.play();
    }
  }
  playAnimalSound(n: number) {
    this.audioSource.clip = this.animalSoundFxs[n];
    this.audioSource.play();
  }
}
