import {
  _decorator,
  Component,
  instantiate,
  Node,
  Prefab,
  Vec2,
  EventTouch,
  RigidBody2D,
  input,
  Input,
} from "cc";
import { AudioManager } from "./AudioManager";
import { Ball } from "./Ball";
import { GameManager } from "./GameManager";

const { ccclass, property } = _decorator;

@ccclass("BallGenerator")
export class BallGenerator extends Component {
  private static _instance: BallGenerator | null = null;
  private touchEnabled: boolean = false; // Flag to control touch input

  public static getInstance(): BallGenerator {
    return this._instance!;
  }

  @property({ type: Prefab })
  public balls: Prefab[] = [];
  private ballQueue: Node[] = [];

  @property({ type: [Prefab] })
  public blastFxs: Prefab[] = [];
  private isDropping: boolean = false;
  private ballReadytoDrop: boolean = false;
  private activeBall: Node | null = null;

  @property({ type: Node })
  public ballHolder: Node | null = null;
  @property({ type: Node })
  public gameBallHolder: Node | null = null;
  @property({ type: Vec2 })
  public ballSpawnPos: Vec2 = new Vec2(200, 600);
  @property({ type: Vec2 })
  public ballSpawnPosCenter: Vec2 = new Vec2(0, 600);
  private customOrder: number[] = [
    0, 0, 1, 1, 2, 2, 3, 1, 1, 0, 4, 3, 3, 2, 1, 0, 5, 0, 1, 1, 0, 6, 2, 3, 2,
    2, 1, 4, 4,
  ]; // Define your custom order here
  private currentIndex: number = 0;
  onLoad() {
    BallGenerator._instance = this;
    this.disableTouchInput();
  }

  startGame() {
    const balls = this.ballHolder?.children;
    if (balls) {
      for (let i = 0; i < balls.length; i++) {
        const ball = balls[i];
        ball.destroy();
      }
    }
    const gameBalls = this.gameBallHolder?.children;
    if (gameBalls) {
      for (let i = 0; i < gameBalls.length; i++) {
        const ball = gameBalls[i];
        ball.destroy();
      }
    }
    this.ballQueue = [];
    this.spawnInitialBalls();
  }
  enableTouchInput() {
    this.touchEnabled = true;
    input.on(Input.EventType.TOUCH_START, this.onTouchStart, this);
    input.on(Input.EventType.TOUCH_END, this.onTouchEnd, this);
    input.on(Input.EventType.TOUCH_MOVE, this.onTouchMove, this);
  }
  disableTouchInput() {
    this.touchEnabled = false;
    input.off(Input.EventType.TOUCH_START, this.onTouchStart, this);
    input.off(Input.EventType.TOUCH_END, this.onTouchEnd, this);
    input.off(Input.EventType.TOUCH_MOVE, this.onTouchMove, this);
  }

  blast(pos: any) {
    const randomIndex = Math.floor(Math.random() * this.blastFxs.length);
    const blastFx = instantiate(this.blastFxs[randomIndex]);
    blastFx.setPosition(pos);
    blastFx.setParent(this.gameBallHolder);
    this.gameBallHolder?.addChild(blastFx);

    // Destroy the FX after a second
    this.scheduleOnce(() => {
      blastFx.destroy();
    }, 0.1);
  }

  spawnInitialBalls() {
    for (let i = 0; i < 3; i++) {
      const spawnPosition = new Vec2(
        this.ballSpawnPos.x + 300 - i * 100,
        this.ballSpawnPos.y + 10
      );
      this.spawnBallWithDelay(spawnPosition, 1000 * i);
    }
  }

  onTouchStart(event: EventTouch) {}

  onTouchEnd(event: EventTouch) {
    // Implement tap detection logic if required

    this.dropTheBall();
  }

  onTouchMove(event: EventTouch) {
    this.moveQueuedBallWithTouch(event);
  }

  onDestroy() {
    input.off(Input.EventType.TOUCH_START, this.onTouchStart, this);
  }

  async spawnBallWithDelay(spawnPoint: Vec2, delay: number) {
    return new Promise<void>((resolve) => {
      setTimeout(() => {
        this.spawnBall(spawnPoint);
        resolve();
      }, delay);
    });
  }
  spawnBallAtCollision(spawnPoint: any, ballCount: number) {
    if (this.balls[ballCount]) {
      // Using the first prefab from the list
      AudioManager.getInstance().playSoundDroppedBall;
      GameManager.getInstance().updateScore(ballCount);
      const ball = instantiate(this.balls[ballCount]);
      ball.addComponent(Ball);
      ball.setScale(1.3 * ballCount, 1.3 * ballCount);
      ball.setParent(this.gameBallHolder);
      ball.setPosition(spawnPoint);
      ball.getComponent(RigidBody2D).enabled = true;
      ball.active = true;
    }
  }

  spawnBall(spawnPoint: Vec2) {
    if (this.balls.length === 0) {
      console.error("No ball prefabs available!");
      return;
    }

    const ballPrefab = this.balls[0]; // Using the first prefab from the list

    const ball = instantiate(ballPrefab);
    ball.addComponent(Ball);

    if (this.ballHolder) {
      this.ballHolder.addChild(ball);
      this.ballQueue.push(ball);
    } else {
      console.error("Ball Holder node not assigned!");
    }

    if (this.ballQueue.length === 3) {
      this.enableTouchInput();
      setTimeout(() => {
        this.moveBallToCenterAndSpawnNew(2000);
      }, 1000);
      return;
    }
  }

  async moveBallToCenterAndSpawnNew(duration: number) {
    if (GameManager.getInstance().isGameOver) {
      return;
    } else this.ballReadytoDrop = false;
    if (this.ballQueue.length > 0) {
      this.activeBall = this.ballQueue.shift();

      if (this.activeBall) {
        this.activeBall.parent = this.gameBallHolder;
        const ballNumber = +this.activeBall.name;
        const scaleValue = 1 + (1 * ballNumber) / 2;

        this.activeBall.setScale(scaleValue, scaleValue);
        this.activeBall.setPosition(
          this.ballSpawnPosCenter.x,
          this.ballSpawnPosCenter.y
        );

        setTimeout(() => {
          const ballIndex =
            this.customOrder[this.currentIndex % this.customOrder.length];
          //this.customOrder[this.currentIndex];

          const newBall = instantiate(this.balls[ballIndex]);

          newBall.addComponent(Ball);
          newBall.setPosition(this.ballSpawnPos.x + 300, this.ballSpawnPos.y);

          if (this.ballHolder) {
            this.ballHolder.addChild(newBall);
            this.ballQueue.push(newBall);
          } else {
            console.error("Ball Holder node not assigned!");
          }
        }, 1000);
      }
    }
    this.ballReadytoDrop = true;
  }
  moveQueuedBallWithTouch(event: EventTouch) {
    if (this.activeBall) {
      const touchDelta = event.getDelta();
      const newPositionX = this.activeBall.position.x + touchDelta.x;
      this.activeBall.setPosition(newPositionX, this.ballSpawnPos.y);
    }
  }

  dropTheBall() {
    if (!this.isDropping && this.ballReadytoDrop) {
      this.activeBall.getComponent(RigidBody2D).enabled = true;
      this.isDropping = true;
      AudioManager.getInstance().playBallDropping();

      this.currentIndex++;
      setTimeout(() => {
        this.moveBallToCenterAndSpawnNew(3000);

        this.isDropping = false;
      }, 1000);
    }
  }
}
