import {
  _decorator,
  Component,
  Node,
  director,
  Vec3,
  Tween,
  v3,
  Label,
  tween,
  easing,
} from "cc";
import { BallGenerator } from "./ballGenerator";
import { AudioManager } from "./AudioManager";

const { ccclass, property } = _decorator;

@ccclass("GameManager")
export class GameManager extends Component {
  private static _instance: GameManager | null = null;
  private touchEnabled: boolean = false; // Flag to control touch input
  public isGameOver: boolean = false;
  public static getInstance(): GameManager {
    return this._instance!;
  }

  @property({ type: Node })
  mainMenu: Node | null = null;

  @property({ type: Label })
  scoreText: Label | null = null;

  @property({ type: Node })
  gameScreen: Node | null = null;

  @property({ type: Node })
  gameOverScreen: Node | null = null;

  @property({ type: Label })
  scoreLabel: Label | null = null;
  private currentScore: { value: number } = { value: 0 }; // Define currentScore property
  private finalScore: number = 0;
  private duration: number = 2; // Duration for the animation in seconds
  private finalDuration: number = 0.5;
  onLoad() {
    GameManager._instance = this;
    this.showMainMenu();
  }

  showMainMenu() {
    this.isGameOver = false;
    if (this.mainMenu) {
      this.mainMenu.active = true;
      this.moveNode(this.mainMenu, v3(-3000, 0, 0), v3(0, 0, 0));
    }
    if (this.gameOverScreen) {
      this.moveNode(this.gameOverScreen, v3(0, 0, 0), v3(3000, 0, 0));
    }
  }

  startGame() {
    this.updateScore(1);
    if (this.gameScreen) {
      this.gameScreen.active = true;
      this.moveNode(this.mainMenu, v3(0, 0, 0), v3(-3000, 0, 0));
      this.moveNode(this.gameScreen, v3(3000, 0, 0), v3(0, 0, 0));
    }
    BallGenerator.getInstance().startGame();
  }

  gameOver() {
    if (!this.isGameOver) {
      if (this.gameOverScreen) {
        this.gameOverScreen.active = true;
        this.moveNode(this.gameOverScreen, v3(-3000, 0, 0), v3(0, 0, 0));
      }
      this.typewriteValue(30444);
      AudioManager.getInstance().playBackgroundMusicDanger();
      this.isGameOver = true;
    }
  }

  moveNode(node: Node, startPos: Vec3, endPos: Vec3) {
    node.setPosition(startPos);
    const tween = new Tween(node);
    tween.to(0.5, { position: endPos }, { easing: "sineOut" });
    tween.start();
  }
  updateScore(ballCount: number) {
    console.log("updatingScore");
    const newScore = ballCount * 100;
    this.scoreText.string = "" + newScore;
  }

  onRetryButtonClicked() {
    this.isGameOver = false;
    // director.loadScene("GameScene"); // Replace "GameScene" with the name of your game scene
    this.updateScore(1);
    if (this.gameOverScreen) {
      this.moveNode(this.gameOverScreen, v3(0, 0, 0), v3(800, 0, 0));
    }
    BallGenerator.getInstance().startGame();
  }

  // Call this method to update the final score with an animation effect
  typewriteValue(value: number) {
    let initialValue = 0;
    let currentValue = { value: initialValue };

    tween(currentValue)
      .to(
        this.duration,
        { value: value },
        {
          onUpdate: () => {
            // Update the label with the current value (simulating typewriting)
            if (this.scoreLabel) {
              this.scoreLabel.string = Math.floor(
                currentValue.value
              ).toString();
            }
          },
          onComplete: () => {
            // After the typewriting animation completes, update the label to the final value
            this.updateFinalValue(value);
          },
        }
      )
      .start(); // Start the typewriting tween animation
  }

  updateFinalValue(value: number) {
    let startValue = parseInt(this.scoreLabel?.string || "0");
    let finalValue = { value: startValue };

    tween(finalValue)
      .to(
        this.finalDuration,
        { value: value },
        {
          onUpdate: () => {
            // Update the label with the final value gradually
            if (this.scoreLabel) {
              this.scoreLabel.string = Math.floor(finalValue.value).toString();
            }
          },
        }
      )
      .start(); // Start the final value update tween animation
  }
}
